* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
}

html {
  font-size: 16px;
  scroll-behavior: smooth;
}

body {
  background-color: #010102;
}

.overflow {
  overflow: hidden;
}

.container {
  width: 85%;
  margin: 0 auto;
}

.goTop {
  font-size: 3rem;
  color: #c0aa83;
  position: fixed;
  bottom: 15px;
  right: 15px;
  z-index: 2000 !important;
  cursor: pointer;
  display: none;
}

.header {
  height: 100vh;
  background-color: #010102;
  background-image: url(../img/banner-min.png);
  background-size: cover;
}
.header__container {
  height: 100%;
  width: 95%;
  margin: 0 auto;
}
@media (max-width: 992px) {
  .header__container {
    padding: 0 2rem;
  }
}
.header .navigation {
  position: relative;
  z-index: 100;
  display: flex;
  justify-content: space-between;
  padding: 2rem 0;
  align-items: center;
  width: 100%;
}
.header .navigation--active {
  position: fixed;
  top: 0;
  left: 0;
  padding: 2rem 3.4rem;
}
@media (max-width: 768px) {
  .header .navigation--active {
    padding: 2rem 2.7rem;
  }
}
.header .navigation__logo {
  z-index: 11;
}
.header .navigation__logo .linkLogo {
  display: flex;
  align-items: center;
  text-transform: uppercase;
  font-size: 1.4rem;
  color: #ffffff;
  font-family: "Montserrat", serif;
  text-align: left;
}
.header .navigation__logo .linkLogo__image {
  width: 3.5rem;
  margin-right: 1rem;
}
.header .navigation__icon {
  z-index: 11;
  color: #ffffff;
  font-size: 1.5rem;
  display: none;
  cursor: pointer;
}
@media (max-width: 992px) {
  .header .navigation__icon {
    display: block;
  }
}
.header .navigation__group {
  display: flex;
  justify-content: space-between;
  width: 60%;
}
@media (max-width: 992px) {
  .header .navigation__group {
    opacity: 0;
    transform: translateX(-100%);
    transition: transform 0.5s ease, opacity 0.5s ease;
    z-index: 10;
    padding: 0 4rem;
    background-color: rgba(19, 19, 26, 0.2);
    backdrop-filter: blur(1rem);
    width: 100%;
    height: 100%;
    position: absolute;
    padding: 9rem 0 3rem 2rem;
    top: 0;
    left: 0;
    right: 0;
    flex-direction: column;
  }
  .header .navigation__group--active {
    opacity: 1;
    transform: translateX(0%);
    position: fixed;
  }
}
.header .navigation__links {
  display: flex;
}
@media (max-width: 992px) {
  .header .navigation__links {
    flex-direction: column;
  }
}
.header .navigation__list {
  padding: 0 1rem;
}
@media (max-width: 992px) {
  .header .navigation__list {
    margin-bottom: 2rem;
  }
}
.header .navigation__link {
  color: #ffffff;
  font-family: "Montserrat", serif;
  font-size: 1.1rem;
  color: #ffffff;
  font-family: "Montserrat", serif;
  text-align: left;
}
.header .navigation__link--active {
  color: #c0aa83;
  font-weight: 600;
  text-shadow: 1px 1px 10px #ffffff;
}
.header .navigation__networks a {
  margin-right: 2rem;
}
.header .navigation__networks a:last-child {
  margin-right: 0rem;
}
.header .navigation__networks a i {
  font-size: 1.2rem;
  color: #ffffff;
  transition: color 0.5s ease;
}
.header .navigation__networks a i:hover {
  color: #c0aa83;
}
.header__content {
  position: absolute;
  top: 0;
  height: 100%;
  display: flex;
  align-items: center;
}
@media (max-width: 992px) {
  .header__content {
    align-items: center;
    padding-bottom: 6rem;
  }
}
.header__description {
  max-width: 650px;
}
.header__title {
  font-size: 3rem;
  color: #c0aa83;
  font-family: "Montserrat", serif;
  text-align: left;
  text-transform: uppercase;
  margin-bottom: 1rem;
}
.header__text {
  font-size: 1.2rem;
  color: #ffffff;
  font-family: "Nunito Sans", sans-serif;
  text-align: left;
  line-height: 2;
  margin-bottom: 2rem;
}
.header .btn {
  background-color: #c0aa83;
  color: #ffffff;
  padding: 1rem 2.5rem;
  border-radius: 5px;
  border: none;
  font-size: 1.2rem;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.header .btn::before {
  content: "See Products";
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: -100%;
  top: 0%;
  height: 100%;
  width: 0;
  background-color: #010102;
  transition: width 0.5s ease-in, left 0.5s ease-in;
}
.header .btn:hover::before {
  width: 100%;
  left: 0%;
}
.header .btn:hover {
  border: 1px solid #ffffff;
}
.header__image {
  width: 650px;
}

.mainHome .subtitle {
  font-size: 2rem;
  color: #c0aa83;
  font-family: "Montserrat", serif;
  text-align: center;
  margin-bottom: 2rem;
}
.mainHome .productSection {
  padding: 3rem 0;
}
.mainHome .products {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}
@media (max-width: 992px) {
  .mainHome .products {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 768px) {
  .mainHome .products {
    grid-template-columns: repeat(1, 1fr);
  }
}
.mainHome .products .product {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 3.5rem 2.3rem;
  transition: transform 0.5s ease;
}
.mainHome .products .product:nth-child(1) {
  background: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.7)), url(../img/fruitSalad.jpg);
  background-size: cover;
  background-position: center;
}
@media (max-width: 992px) {
  .mainHome .products .product:nth-child(1) {
    grid-column: 1/2;
  }
}
@media (max-width: 768px) {
  .mainHome .products .product:nth-child(1) {
    grid-column: auto;
  }
}
.mainHome .products .product:nth-child(2) {
  background-image: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.7)), url(../img/cakes.jpg);
  background-size: cover;
  background-position: center;
}
@media (max-width: 992px) {
  .mainHome .products .product:nth-child(2) {
    grid-column: 2/3;
  }
}
@media (max-width: 768px) {
  .mainHome .products .product:nth-child(2) {
    grid-column: auto;
  }
}
.mainHome .products .product:nth-child(3) {
  background-image: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.7)), url(../img/donut.jpg);
  background-size: cover;
  background-position: center;
}
@media (max-width: 992px) {
  .mainHome .products .product:nth-child(3) {
    grid-column: 1/3;
  }
}
@media (max-width: 768px) {
  .mainHome .products .product:nth-child(3) {
    grid-column: auto;
  }
}
.mainHome .products .product:hover {
  transform: scale(1.03);
}
.mainHome .products .product__image {
  width: 100%;
  object-fit: cover;
}
.mainHome .products .product__title {
  display: inline;
  font-size: 1.3rem;
  color: #ffffff;
  font-family: "Montserrat", serif;
  text-align: left;
  margin-bottom: 2rem;
  position: relative;
}
.mainHome .products .product__title::after {
  position: absolute;
  bottom: -10px;
  left: 0;
  content: "";
  width: 10%;
  height: 5px;
  background-color: #c0aa83;
}
.mainHome .products .product__text {
  font-size: 1rem;
  color: #ffffff;
  font-family: "Nunito Sans", sans-serif;
  text-align: center;
  line-height: 2;
}

.mainHome .special {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: 2rem;
}
@media (max-width: 992px) {
  .mainHome .special {
    grid-template-columns: repeat(1, 1fr);
  }
}
.mainHome .special__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}
@media (max-width: 992px) {
  .mainHome .special__image {
    display: none;
  }
}
.mainHome .special__right {
  background-color: #ffffff;
  padding: 5rem 3rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.mainHome .special__title {
  font-size: 2rem;
  color: #010102;
  font-family: "Montserrat", serif;
  text-align: left;
  margin-bottom: 1.5rem;
  text-transform: uppercase;
}
.mainHome .special__text {
  font-size: 1rem;
  color: #707070;
  font-family: "Nunito Sans", sans-serif;
  text-align: left;
  line-height: 2;
  margin-bottom: 1rem;
}

.ordersParallax {
