.galleryShop {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 450px);
}
@media (max-width: 768px) {
  .galleryShop {
    grid-template-columns: repeat(1, 1fr);
    grid-auto-flow: row;
  }
}
.galleryShop__link {
  overflow: hidden;
}
.galleryShop__link:nth-child(1) {
  grid-column: 1/2;
  grid-row: 1/3;
}
@media (max-width: 768px) {
  .galleryShop__link:nth-child(1) {
    grid-row: 1/2;
    grid-column: auto;
  }
}
.galleryShop__link:nth-child(2) {
  grid-column: 2/4;
  grid-row: 1/2;
}
@media (max-width: 768px) {
  .galleryShop__link:nth-child(2) {
    grid-row: 2/3;
    grid-column: auto;
  }
}
.galleryShop__link:nth-child(3) {
  grid-column: 4/5;
  grid-row: 1/2;
}
@media (max-width: 768px) {
  .galleryShop__link:nth-child(3) {
    grid-row: 3/4;
    grid-column: auto;
  }
}
.galleryShop__link:nth-child(4) {
  grid-column: 2/3;
  grid-row: 2/3;
}
@media (max-width: 768px) {
  .galleryShop__link:nth-child(4) {
    grid-row: 4/5;
    grid-column: auto;
  }
}
.galleryShop__link:nth-child(5) {
  grid-column: 3/5;
  grid-row: 2/3;
}
@media (max-width: 768px) {
  .galleryShop__link:nth-child(5) {
    grid-row: 5/6;
    grid-column: auto;
  }
}
.galleryShop__image {
  display: inline-block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transition: transform 0.5s ease;
}
.galleryShop__image:hover {
  transform: scale(1.05);
}

